	export default {
		onLaunch: function() {
			// 隐藏uni自带的默认底部导航栏TabBar
			uni.hideTabBar()
		},
		onShow: function() {
			
		},
		onHide: function() {
			
		}
	}
</script>

<style>
	/*每个页面公共css */
	/* 具有底部导航栏页面的样式 */
	.main {
		overflow: hidden;
		height: calc(100vh - 91px);
	}
	/* 不具有底部导航栏页面的样式 */
	._main{
		height: calc(100vh - 45px);
	}
</style>
